import React from 'react'
import getData from './js/init'
const Navbar = () => {
    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
            <div className="container-fluid">
                <a className="navbar-brand" href="#">Examen</a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <a className="nav-link active" aria-current="page" href="#">React</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#">TypeScript</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link disabled">Rastrear</a>
                        </li>
                    </ul>
                    <div className="d-flex">
                        <input className="form-control me-2" id="zipcode" type="search" placeholder="Código postal" aria-label="Search" />
                        <button className="btn btn-success" type="submit" onClick={getData}>Buscar</button>
                    </div>
                </div>
            </div>
        </nav>
    )
}

export default Navbar
